@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

:root * {
  font-family: 'Oswald', sans-serif;
  transition: all 2.2s cubic-bezier(0.5, 0, 0.1, 1);
  box-sizing: border-box;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  font-size: 1rem;
  font-family: 'Oswald', sans-serif;
  color: #000;

  /*    transition: background-color 1s ease;*/
}


ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;

}

img {
  vertical-align: bottom;
}

adrress,
em {
  flex-shrink: normal;
}

button {
  border: none;
  background: transparent;


}


.Headerstyle h1 {
  font-size: 5rem;

}

.more {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 60px;
  background-color: #00b29c;
  color: white;
  border-radius: 50px;


}

.underline_m {
  margin: 20px 0;
}

.underline {
  line-height: 1.2;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, "Apple Color Emoji", Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.2rem;
  font-weight: 700;
  background-image: linear-gradient(transparent calc(100% - 3px), #000 3px);
  background-repeat: no-repeat;
  background-size: 0% 100%;
  transition: background-size 0.8s;
  color: #000;
  cursor: pointer;
}

.underline.yellow {
  background-image: linear-gradient(transparent 60%, #F8CD07 40%);
}

.underline:hover {
  background-size: 100% 100%;
}

.orange {
  color: #000;
  display: inline-block;
  margin: 0;
  text-transform: uppercase;

}

.orange:after {
  display: block;
  content: '';
  border-bottom: solid 3px #F8CD07;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
}

.orange:hover:after {
  transform: scaleX(1);
}


.imgwrap {
  border: 1px solid lightgray;
  border-radius: 50px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;

}

.imgwrap img {
  width: 140%;

}

.imgwrap:hover img {
  width: 110%;
}

.Headerstyle {
  /* width: 1200px; */
  /* margin: 0 auto; */
  height: 100vh;
  color: white;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.913);

}


.mainwrap {
  width: 1200px;
  margin: 0 auto;
  height: 2800px;
}

.mainwrap_height {
  height: 2800px;
}

.title {
  float: left;
  width: 500px;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title h1 {
  font-size: 5rem;

}


.both {
  clear: both;
}

.image {
  float: right;
  /* background-image: url(./oda1.jpg); */
  width: 50%;
  height: 60%;
  overflow: hidden;
  position: sticky;
  top: 100px;
}

.image img {
  object-fit: cover;
  height: inherit;
}


.project {
  background: rgb(255, 255, 255);
  width: 100%;
  margin-top: 100px;
  /* margin: 50px; */
  border-radius: 50px;
  z-index: 50;
  height: inherit;
  /* top: -30px; */
  /* position: relative; */
}

.row_m {
  margin: 0 0 150px 0;
}

.row_m p {
  line-height: 28px;
  text-align: justify;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}